import React from "react";
import { useState, useEffect } from "react";
import { getClubs, getClubsNumber } from "../apiCalls";

export const ClubsScript = () => {
  const [status, setStatus] = useState("");
  const sampledata = [
    {
      name: "Delhi Web3",
      description:
        "In publishing and graphic design, Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document or a typeface without relying on meaningful content. Lorem ipsum may be used as a placeholder before final copy is available",
      members: "52",
      category: "🌐 Web3",
      profilepic:
        "data:image/jpeg;base64,",
      id: 1,
    },
    {
      name: "Ionic Metaverse",
      description:
        "In publishing and graphic design, Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document or a typeface without relying on meaningful content. Lorem ipsum may be used as a placeholder before final copy is available",
      members: "63",
      category: "🪐 Metaverse",

      profilepic:
        "data:image/jpeg;base64,",
      id: 2,
    },
    {
      name: "BIT defi",
      description:
        "In publishing and graphic design, Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document or a typeface without relying on meaningful content. Lorem ipsum may be used as a placeholder before final copy is available",
      members: "105",
      category: "🏦 DeFi",
      profilepic:
        "data:image/jpeg;base64,",
      id: 3,
    },
    {
      name: "Photon experience",
      description:
        "In publishing and graphic design, Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document or a typeface without relying on meaningful content. Lorem ipsum may be used as a placeholder before final copy is available",
      members: "71",
      category: "🎮 GameFi",
      category2 :"gamefi",
      profilepic:
        "data:image/png;base64,",
      id: 4,
    },
  ];
  const [clubData, setClubData] = useState(sampledata);
  const [clubsNumberData, setClubsNumberData] = useState({});
  const [filter, setFilter] = useState("");

  useEffect(() => {
    poppulateClubs();
  }, []);

  const poppulateClubs = async () => {
    getClubs().then((res) => {
      if (res !== "Server error") {
        if (res.status === "Success") {
          setClubData(res.data);
          setStatus("");
        } else {
          setStatus("Something went wrong. Please refresh.");
        }
      } else {
        setStatus("Something went wrong. Please refresh.");
      }
    });
    getClubsNumber().then((res) => {
      if (res !== "Server error") {
        if (res.status === "Success") {
          setClubsNumberData(res.data);
        }
      }
    });
  };
  return { status, setStatus, clubData, clubsNumberData, filter, setFilter };
};

export default ClubsScript;